				sedProduct: '鹦鹉',
				thdProduct: '哈巴狗',
				fstProductInfo: '金丝猴（拉丁学名：Rhinopithecus），毛质柔软，鼻子上翘，有缅甸金丝猴（怒江金丝猴）、川金丝猴、滇金丝猴、黔金丝猴、越南金丝猴5种，其中除缅甸金丝猴和越南金丝猴外，均为中国特有的珍贵动物,金丝猴群栖高山密林中，以浆果、竹笋、苔藓为食，亦喜食鸟蛋等肉类，栖息地海拔很高，身上的长毛可耐寒。5个品种均为珍稀品种，均列为红色物种名录濒危品种',
				sedProductInfo: '鹦鹉是鹦形目（学名：Psittaciformes）众多羽毛艳丽、爱叫的鸟。典型的攀禽，对趾型足，两趾向前两趾向后，适合抓握，鸟喙强劲有力，可以食用硬壳果。羽色鲜艳，常被作为宠物饲养。它们以其美丽的羽毛，善学人语技能的特点，更为人们所欣赏和钟爱。鹦鹉中体形最大的当属紫蓝金刚鹦鹉，体长可达100厘米，最小的是蓝冠短尾鹦鹉 ，体长仅有12厘米',
				thdProductInfo: '哈巴狗即巴哥犬，起源于中国。哈巴狗有很长的历史，其祖先在16世纪被东印度公司的贸易商带到欧洲，成为王室和贵族的宠爱。该犬体型小而壮实，它严肃的外表掩盖着欢快的性情和优秀的品质。哈巴狗非常聪明，性格外向，富有爱心，是忠实的伴侣犬，它适合新手犬主，对儿童和其他宠物很友好。该犬需要定期运动，但不要求太大生活空间，所以是公寓生活的理想犬种',
